          </div>
          <div class="vipnotice">
            <div><span class="vipprice">199</span></div>
            <div class="vipname">VIP</div>
            <div class="viptime"></div>
          </div>
          <div v-if="viplevel<3 && orderlimit==false" @click="buyvipflag && buyvip(1,3)" class="buyvip buyvipwechat">
            <a></a>
          </div>
          <div v-if="viplevel<3 && orderlimit==false" @click="buyvipflag && buyvip(2,3)" class="buyvip buyvipzfb">
            <a></a>
          </div>
          <div v-else-if="viplevel<3 && orderlimit==true" @click="showvip()" class="buyvip buyvipgly">
            <a></a>
          </div>
          <div v-else="" class="hasvip">
            
          </div>
        </li>
        <li v-if="viplevel<5">
          <div class="vipicon">
            <img src="/static/icon/vip-vipicon3.png" alt="">
          </div>
          <div class="vipnotice">
            <div><span class="vipprice">399</span></div>
<!--            <div>vip</div>-->
            <div class="vipname">50%</div>
            <div class="viptime"></div>
          </div>
          <div v-if="viplevel<4 && orderlimit==false" @click="buyvipflag && buyvip(1,4)" class="buyvip buyvipwechat">
            <a></a>
          </div>
          <div v-if="viplevel<4 && orderlimit==false" @click="buyvipflag && buyvip(2,4)" class="buyvip buyvipzfb">
            <a></a>
          </div>
          <div v-else-if="viplevel<4 && orderlimit==true" @click="showvip()" class="buyvip buyvipgly">
            <a></a>
          </div>
