<!--            <a></a>-->
<!--          </div>-->
          <div v-else="" class="hasvip">
            
          </div>
        </li>

        <li v-if="viplevel<6">
          <div class="vipicon">
            <img src="/static/icon/vip-vipicon4.png" alt="">
          </div>
          <div class="vipnotice">
            <div><span class="vipprice">599</span></div>
            <div class="vipname">70%</div>
            <div class="viptime"></div>
          </div>
          <div v-if="viplevel<5 && orderlimit==false" @click="buyvipflag && buyvip(1,5)" class="buyvip buyvipwechat">
            <a></a>
          </div>
          <div v-if="viplevel<5 && orderlimit==false" @click="buyvipflag && buyvip(2,5)" class="buyvip buyvipzfb">
