        </li>

        <li v-if="viplevel<7">
          <div class="vipicon">
            <img src="/static/icon/vip-vipicon5.png" alt="">
          </div>
          <div class="vipnotice">
            <div><span class="vipprice">2999</span></div>
            <div class="vipname"></div>
            <div class="viptime"></div>
          </div>
<!--          <div v-if="viplevel<5 && orderlimit==false" @click="buyvipflag && buyvip(1,5)" class="buyvip buyvipwechat">-->
<!--            <a></a>-->
<!--          </div>-->
<!--          <div v-if="viplevel<5 && orderlimit==false" @click="buyvipflag && buyvip(2,5)" class="buyvip buyvipzfb">-->
<!--            <a></a>-->
<!--          </div>-->
          <div v-if="viplevel<6" @click="showvip()" class="buyvip buyvipgly">
            <a></a>
          </div>
          <div v-else="" class="hasvip">
            
          </div>
        </li>
      </ul>
    </div>

    <!---->
    <div v-show="showpaybox" class="popuppaybox">
      <div class="paybox">
        <svg @click="closepaybox()" class="payboxcloseicon"></svg>
        <div class="payboxalert">
          <p class="payboxalertclass">{{getpaytype()}}{{getpayprice()}}</p>
          <p class="payboxalertdes">{{getpayclasstype()}}</p>
          <p class="payboxalertadd">{{timecount}}5SVIP</p>
          <div class="payboximg">
            <img :src="image_64" alt="">
            <!--<img src="/static/image/vip-loading.png" alt="">-->
          </div>
        </div>

      </div>
    </div>

    <!---->
    <div v-show="showvipbox" class="popuppaybox">
      <div class="paybox">
        <svg @click="closevip()" class="payboxcloseicon"></svg>
        <div class="payboxalert">
          <p class="payboxalertvip"></p>
          <!--<p class="payboxalertdes">{{getpayclasstype()}}</p>-->
          <!--<p class="payboxalertadd">{{timecount}}5SVIP</p>-->
          <div class="payboximg">
            <img src="/static/image/kefuweixin.jpg" alt="">
            <!--<img src="/static/image/vip-loading.png" alt="">-->
          </div>
        </div>

      </div>
    </div>

    <div class="toast" v-show="toastShow">
      <div class="toastbox">
        <div class="toasticonbox">
          <img src="/static/icon/icon-toast.png">
        </div>
        {{toastText}}
      </div>
    </div>

  </div>




</template>

<script>

export default {
  name: 'vip',
  data () {
    return {
      loginType:false,
      viplevel:1,
      outtime:'',
      showpaybox:false,
      paytype:0,
      payclasstype:0,
      image_64:'',
      buyvipflag:true,
      toastShow: false,
      toastText: '',
      timecount:'',
      timer:null,
      aid:'',
      free_limit:false,
      orderlimit:false,
      showvipbox:false,
    }
  },
  mounted: function() {
    this.getData();

  },
  methods:{
    getData:function(){
      this.axios.get("api/vip")
        .then(response =>{
          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
            this.$router.push({
              name: 'Login',
            })
          }
         console.log(response.data)
          this.viplevel=response.data.viplevel
          if(response.data.orderlimit=='yes'){
            this.orderlimit=true
          }else {
            this.orderlimit=false
          }
          if(this.viplevel==2){
            this.outtime=response.data.outtime
//            console.log(this.outtime.indexOf("T"))
            this.outtime=this.outtime.substring(0,this.outtime.indexOf("T"))
//            console.log(this.outtime)
          }
        })
        .catch(function (error) {

        })
    },
    buyvip:function(paytype,classnum){
      this.buyvipflag=false
      this.paytype=paytype
      this.payclasstype=classnum
      this.axios.get("api/order",{
        params: {
          paytype:paytype,
          classnum:classnum,
        }
      })
        .then(response =>{
          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
            this.$router.push({
              name: 'Login',
            })
          }
          console.log(response.data)
          if(response.data.orderstatus == 'fourmin'){
            this.toast("4")
            this.buyvipflag=true
          }else if(response.data.orderstatus=='ok') {
            this.showpaybox=true
            this.image_64=response.data.image_64
            this.aoid=response.data.aoid

            const TIME_COUNT = 234
            if (!this.timer) {
              this.timecount = TIME_COUNT;
              this.timer = setInterval(() => {
                if (this.timecount > 0 && this.timecount <= TIME_COUNT) {
                  this.timecount--
                  if(this.timecount%5==0){
                    this.axios.get("api/queryorder",{
                      params: {
                        aoid:this.aoid,
                      }
                    })
                      .then(response =>{
                          if(response.data.status=='success'){
                            this.getData();
                            this.showpaybox=false
                            this.buyvipflag=true
                            this.image_64=''
                            this.toast("")
                            clearInterval(this.timer)
                            this.timer=null
                            this.timecount=0
                          }else if(response.data.status=='new'){
                            console.log("new")
                          }else {
                            this.showpaybox=false
                            this.buyvipflag=true
                            this.image_64=''
                            clearInterval(this.timer)
                            this.timer=null
                            this.timecount=0
                          }
                      })
                      .catch(function (error) {

                      })
                  }
                } else {
                  this.showpaybox=false
                  this.buyvipflag=true
                  clearInterval(this.timer)
                  this.timer=null
                  this.timecount=0
                }
              }, 1000)
            }
          }else{
            this.toast("")
          }

        })
        .catch(function (error) {

        })
    },
    closepaybox:function(){
      this.showpaybox=false
      this.buyvipflag=true
    },
    getpaytype:function () {
      if(this.payclasstype==2){
        return "VIP"
      }else if(this.payclasstype==3){
        return "VIP"
      }else if(this.payclasstype==4){
        return ""
      }else{
        return ""
      }
    },
    getpayprice:function () {
      if(this.payclasstype==2){
        return "99"
      }else if(this.payclasstype==3){
        return "199"
      }else if(this.payclasstype==4){
        return "399"
      }else{
        return "599"
      }
    },
    getpayclasstype:function () {
      if(this.paytype==1){
        return ""
      }else {
        return ""
      }
    },
    showvip:function(){
      this.showvipbox=true
    },
    closevip:function(){
      this.showvipbox=false
    },
    goBack:function(){
      this.$router.go(-1)
    },
    toast:function(str){
      let v = this
      v.toastText = str
      v.toastShow = true
      setTimeout(function(){
        v.toastShow = false
      }, 1500)
    },
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
