<template>
  <div id='Mine' class="page-container minebg">
    <!---->
    <div class="subminehead">
      <a class="subminehome" @click="gotoIndex()"></a>
      <div class="submineheadtitle"></div>
    </div>

    <div class="minehead">

      <div class="minheadbody">
        <div class="minheadimg"><img src="/static/image/mineheaduser.png"></div>
        <div class="minheadviplevel">{{viplevelname}}</div>
        <div class="minheademail">{{username}}</div>
      </div>

    </div>

    <div class="mineunit">
      <a class="mineunitsub" @click="gotoCollection()">
        <div class="mineunitsubimg">
          <img src="/static/icon/icon-collection.png">
        </div>
        <div class="mineunitsubtext"></div>
      </a>
      <a class="mineunitsub" @click="gotoFoot()">
        <div class="mineunitsubimg">
          <img src="/static/icon/icon-footprint.png">
        </div>
        <div class="mineunitsubtext"></div>
      </a>
    </div>

    <div class="mineunit">
      <a class="mineunitsub" @click="gotoVip()">
        <div class="mineunitsubimg">
          <img src="/static/icon/icon-VIP.png">
        </div>
        <div class="mineunitsubtext">VIP</div>
      </a>
      <a class="mineunitsub" @click="toretail()">
        <div class="mineunitsubimg">
          <img src="/static/icon/icon-getmoney.png">
        </div>
        <div class="mineunitsubtext"></div>
      </a>
    </div>

    <div class="mineunit">
      <a class="mineunitsub" @click="showvip()">
        <div class="mineunitsubimg">
          <img src="/static/icon/icon-customerser.png">
        </div>
        <div class="mineunitsubtext"></div>
      </a>
    </div>

    <div class="minelogout">
      <button @click='logout()'></button>
    </div>

    <footer>ICP20020041</footer>

    <div class="toast" v-show="toastShow">
      <div class="toastbox">
        <div class="toasticonbox">
          <img src="/static/icon/icon-toast.png">
        </div>
        {{toastText}}
      </div>
    </div>

    <!---->
    <div v-show="showvipbox" class="popuppaybox">
      <div class="paybox">
        <svg @click="closevip()" class="payboxcloseicon"></svg>
        <div class="payboxalert">
          <p class="payboxalertvip"></p>
          <!--<p class="payboxalertdes">{{getpayclasstype()}}</p>-->
          <!--<p class="payboxalertadd">{{timecount}}5SVIP</p>-->
          <div class="payboximg">
            <img src="/static/image/kefuweixin.jpg" alt="">
            <!--<img src="/static/image/vip-loading.png" alt="">-->
          </div>
        </div>

      </div>
    </div>

  </div>

</template>

<script>
  import qs from 'qs'
export default {
  name: 'Mine',
  data () {
    return {
      loginType:false,
      toastShow: false,
      toastText: '',
      username:'',
      viplevelname:'',
      showvipbox:false
    }
  },
  mounted: function() {
    this.getData();
  },
  methods:{
    getData:function(){
      this.axios.get("api/mine")
        .then(response =>{
          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
            this.$router.push({
              name: 'Index',
            })
          }
          this.username=response.data.username
          if(response.data.viplevel==1){
            this.viplevelname=''
          }else if(response.data.viplevel==2){
            this.viplevelname='VIP'
          }else if(response.data.viplevel==3){
            this.viplevelname='VIP'
          }else if(response.data.viplevel==4){
            this.viplevelname=""
          }else if(response.data.viplevel==5){
            this.viplevelname=""
          }else{
            this.viplevelname=""
          }
        })
        .catch(function (error) {

        })
    },
    logout: function () {

      this.axios.post("api/logout",
        qs.stringify({
        })
      )
        .then(response => {
          if (response.data.logoutType=='success') {
            this.toast("")
            this.$router.push({
              name: 'Index',
            })
          }
        })
        .catch(function (error) {
        })

    },
    toast:function(str){
      let v = this
      v.toastText = str
      v.toastShow = true
      setTimeout(function(){
        v.toastShow = false
      }, 1500)
    },
    gotoIndex: function () {
      this.$router.push({
        name: 'Index',
      })
    },
    gotoCollection: function () {
      this.$router.push({
        name: 'Collection',
      })
    },
    gotoFoot: function () {
      this.$router.push({
        name: 'Foot',
      })
    },
    gotoVip: function () {
      this.$router.push({
        name: 'Vip',
      })
    },
    showvip:function(){
      this.showvipbox=true
    },
    closevip:function(){
      this.showvipbox=false
    },
    toretail:function(){
      if(this.viplevelname==''){
        this.toast("")
      }else if(this.viplevelname=='VIP'){
        this.toast("VIP")
      }else if(this.viplevelname==''){
        this.toast("")
      }else {
        this.$router.push({
          name: 'Retail',
        })
      }
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



